	module.exports = {"data":{"markdownRemark":{"id":"/Users/jacobduchen/Desktop/tick_killers/gatsby-starter-business/src/pages/blog/we-kill-deer-with-trained-hunters-with-crossbows.md absPath of file >>> MarkdownRemark","html":"<p>Ticks depend on deer to survive in their second year of life. They are also over populated in most of the United States. In regions with high deer-tick populations, it is guaranteed there are high amounts of deer!</p>\n<p>We are not talking about Bambi here. We are talking about large, strong, fast, and overfed deer who continue to over-populate because of climate, environmental, and predator-less eco-systems.</p>\n<p>We have a trained team of deer hunters with crossbows who literally kill as many deer as they are allowed to in a safe and sustainable fashion.</p>","fields":{"slug":"/blog/we-kill-deer-with-trained-hunters-with-crossbows/"},"frontmatter":{"date":"March 29, 2018","title":"We Kill Deer When Appropriate","cover":"/img/deerhead.png","meta_title":"Lorem Ipsum Dolor Situm","meta_description":"We kill deer when appropriate to deter tick populations.","tags":["deer","crossbow","tick"]}}},"pathContext":{"id":"/Users/jacobduchen/Desktop/tick_killers/gatsby-starter-business/src/pages/blog/we-kill-deer-with-trained-hunters-with-crossbows.md absPath of file >>> MarkdownRemark"}}

